body{
    background-repeat: no-repeat ;
    background-size: cover;
}
.head{
    display: block;
    font-size: 50px;
    color: lime;
    font-family: 'Times New Roman', Times, serif;
}
.App{
    text-align: center;
    
}

.result p {
    font-size: 40px;
    margin: 40px;
    color: black;
    text-align: right;
    height: 50px;
    line-height: 70px;
  }
.calculator{
    max-width: 400px;
     margin: auto;
     background-color:lightsteelblue;
     height: 550px;
     width: 380px;
    
     border-radius: 10px;
}

.buttons button{
    height: 60px;
    width: 60px;
    font-size: 25px;
    margin: 15px;
    padding: 0px;
    background-color: lightseagreen;
    border-radius: 12px;
    cursor: pointer;
    font-weight: bold;
}
.buttons button:focus{
    outline: none;
}
.buttons button:hover{
    background-color: ivory;
}
button.sp{
 background-color:lightsalmon;
 cursor: pointer;
}
button.c{
    background-color:lightslategrey;
}
button:nth-child(12){
    background-color: mediumpurple;
}
button:nth-child(16){
    background-color: yellow;
}
button:nth-child(20){
    background-color: tomato;
}
@media (max-width: 600px) {
    body {
        background-image: url(mobile.jpg);
    }
}
@media (min-width: 1025px) {
    body {
	    background-image: url(images.jpg);
   }
}